/*========== plan details css start here ==========*/
.plan-details-section {
    position: relative;
    z-index: 1;
    overflow: hidden;

    &__shape {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        height: 100%;
    }

    &::before {
        position: absolute;
        content: "";
        width: 520px;
        height: 500px;
        left: -400px;
        top: -330px;
        z-index: -1;
        background: linear-gradient(180deg, rgba(219, 223, 240, 0.7) 0%, rgba(62, 79, 152, 0.7) 100%);
        filter: blur(300px);
    }
}
.plan-details {
    &__item {
        margin-top: 24px;
    }
    &__title {
        margin-bottom: 24px;
        @include msm-screen {
            margin-bottom: 10px;
        }
    }
}
.content-list-wrapper {
    gap: 16px 30px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
    max-width: 650px;
    .form--check {
        margin-bottom: 16px;
        @include msm-screen {
            margin-bottom: 10px;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
}
.feature-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
    gap: 16px;
    .feature-item {
        background-color: hsl(var(--section-bg));
        padding: 20px 12px;
        border-radius: 8px;
        text-align: center;
        &__icon {
            max-width: 60px;
            margin: 0 auto;
            margin-bottom: 16px;
        }
        &__text {
            color: hsl(var(--heading-color));
            margin-bottom: 4px;
        }
        &__title {
            font-family: var(--body-font);
            margin-bottom: 0;
        }
    }
}

/*============ plan details css end here ==========*/