/* ====================== Section Heading ==================== */
.section-heading {
  text-align: center;
  margin-bottom: 50px;
  position: relative;
  z-index: 1;

  @include lg-screen {
    margin-bottom: 40px;
  }
  @include md-screen {
    margin-bottom: 30px;
  }
  @include sm-screen {
    margin-bottom: 20px;
  }

  &__title {
    margin-bottom: 24px;
    @include sm-screen {
      margin-bottom: 16px;
    }
  }
  &__subtitle {
    font-size: 14px;
    background-color: hsl(var(--section-bg));
    padding: 11px 16px;
    line-height: 1;
    border-radius: 8px;
    font-weight: 600;
    color: hsl(var(--heading-color));
    margin-bottom: 10px;
  }
  &__desc {
    max-width: 670px;
    margin-left: auto;
    margin-right: auto;
    line-height: 1.8;
    font-size: clampCal(16px, 18px);
  }

  // Style left
  &.style-left {
    text-align: left;

    .section-heading {
      &__desc {
        margin-left: 0;
      }
    }
  }
}

/* ====================== Section Heading End ==================== */