/* Fully Fit image Css */
.fit-image, .dashboard .user-info__icon img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* ============================= Display Flex Css Start ============================= */
.flex-wrap, .form--radio, .form--check {
  display: flex;
  flex-wrap: wrap;
}

.flex-align, .dashboard .user-info__button, .action-buttons {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.flex-center {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.flex-between {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

