  font-size: 14px;
  color: hsl(var(--heading-color));
}

.payment-card__body {
  padding: 40px;
}

@media screen and (max-width: 575px) {
  .payment-card__body {
    padding: 20px;
  }
}
/*============== insurance plan page css end here ==============*/
/*================ confirmation page start  css ====================*/
.confirmation-container {
  height: calc(100vh - 140px);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: hsl(var(--white));
  padding: 30px;
  box-shadow: var(--box-shadow-two);
  border-radius: 8px;
}

@media screen and (max-width: 424px) {
  .confirmation-container {
    padding: 15px;
  }
}
.confirmation-container__icon {
  text-align: center;
  display: block;
  font-size: 60px;
  color: hsl(var(--base));
  margin-bottom: 30px;
  line-height: 1;
}

@media screen and (max-width: 575px) {
  .confirmation-container__icon {
    margin-bottom: 15px;
  }
}
.confirmation-container__title {
  text-align: center;
  margin-bottom: 16px;
  color: hsl(var(--base));
}

.confirmation-container__text {
  text-align: center;
  font-size: 18px;
  margin-bottom: 24px;
  color: hsl(var(--base));
}

.confirmation-container__body {
  max-width: 650px;
}

.confirmation-container .confirmation-card {
  max-width: 500px;
  margin: 0 auto;
  background-color: hsl(var(--section-bg));
  padding: 24px;
  border-radius: 8px;
}

@media screen and (max-width: 424px) {
  .confirmation-container .confirmation-card {
    padding: 15px;
  }
}
.notification-item .form-check-label {
  font-size: 16px !important;
}

/*==================== confirmation page end css ====================*/
/*======= setting page css start here =======*/
.authentication-item__top {
  display: flex;
  gap: 20px 50px;
  align-items: center;
  margin-bottom: 24px;
}

@media screen and (max-width: 575px) {
  .authentication-item__top {
    margin-bottom: 16px;
  }
}
.authentication-item__title {
  margin-bottom: 5px;
}

.authentication-item__desc {
  font-size: 14px;
  color: hsl(var(--heading-color));
}

/*======== claim details css start here ========*/
.single-claim-item {
  margin-bottom: 40px;
}

.single-claim-item:last-child {
  margin-bottom: 0;
}

.uploader-thumb {
  background-color: hsl(var(--white));
  padding: 24px;
  border-radius: 8px;
}

.uploader-thumb__top {
  margin-bottom: 16px;
}

.uploader-thumb__top .title {
  margin-bottom: 6px;
  color: hsl(var(--heading-color));
  font-weight: 600;
}

.image-uploader {
  border: 1px dashed hsl(var(--border-color));
  border-radius: 8px;
}

.image-uploader .upload-text {
  text-align: center;
}

.temp-active {
  transition: 0.4s linear;
}

/* ======================  Pages Design End  ======================*/