/*========== insurance section css start here ==========*/
.insurance-section {
    position: relative;
    z-index: 1;
    overflow: hidden;
    &__shape {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        height: 100%;
      }
      &::before {
        position: absolute;
        content: "";
        width: 520px;
        height: 520px;
        left: -400px;
        top: -330px;
        z-index: -1;
        background: linear-gradient(180deg, rgba(219, 223, 240, 0.7) 0%, rgba(62, 79, 152, 0.7) 100%);
        filter: blur(700px);
      }
}
.insurance-form {
    border: 1px solid hsl(var(--border-color)/.2);
    background-color: hsl(var(--section-bg) / 0.2);
    padding: 20px;
    border-radius: 16px;
}
.check-wrapper {
    display: flex;
    align-items: center;
    gap: 6px;
    flex-wrap: wrap;
    .type--check {
        position: relative;
        padding: 11px 14px;
        line-height: 1; 
        border: 1px solid hsl(var(--border-color));
        border-radius: 8px;
        display: inline-flex;
        cursor: pointer;
        gap: 10px;
        align-items: center;
        min-height: 40px;
        .type--input {
            position: relative;
            border-radius: 4px;
            width: 16px;
            height: 16px;
            border: 1px solid hsl(var(--border-color));
            cursor: pointer;
            &::before {
                position: absolute;
                content: '\f00c';
                font-family: 'Font Awesome 5 Free';
                font-weight: 900;
                color: hsl(var(--body-color));
                @include fs-12;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                opacity: 0;
              }
        }
        .type-text {
            font-size: 14px;
            color: hsl(var(--heading-color));
        }
    }
}
.type--check:has(input:checked) {
    background-color: hsl(var(--section-bg));
    border-color: hsl(var(--base));
}
.type--check:has(input:checked) .type--input::before {
    opacity: 1;
    color: hsl(var(--base));
}
.type--check:has(input:checked) .type--input {
    border-color: hsl(var(--base));
}
/*========== insurance section css end here ==========*/
