/* ============= Footer Start Here ======================= */
.footer-area {
  background-color: hsl(var(--dark));
  color: hsl(var(--white));
  margin-top: auto;
  overflow: hidden;
  position: relative;
  &::after, &::before {
    position: absolute;
    content: "";
    top: 50%;
    transform: translateY(-50%);
    left: -500px;
    background: linear-gradient(180deg, rgba(219, 223, 240, 0.7) 0%, rgba(62, 79, 152, 0.7) 100%);
    height: 600px;
    width: 600px;
    filter: blur(250px);
    border-radius: 50%;
  }
  &::before {
    left: unset;
    right: -500px;
  }
}

.footer-item {
  &__logo {
    margin-bottom: 24px;

    a {
      img {
        width: 100%;
        height: 100%;
        max-width: 190px;
        max-height: 64px;
      }
    }
  }
  .title {
    color: hsl(var(--white));
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 18px;
  }
  &__title {
    color: hsl(var(--white));
    margin-bottom: 24px;
    text-decoration: underline;
    font-weight: 600;
    font-size: 18px;
    @include sm-screen {
      margin-bottom: 16px;
    }
  }
  &__desc {
    color: hsl(var(--white));
    font-size: 14px;
  }
  .social-list-wrapper {
    margin-top: 24px;
    @include md-screen {
      margin-top: 20px;
    }
    @include msm-screen {
      margin-top: 15px;
    }
    &__title {
      color: hsl(var(--white));
      margin-bottom: 16px;
      text-decoration: underline;
      font-weight: 600;
      font-size: 18px;
    }
 
  }

}

/* Footer List Item */
.footer-menu {
  display: flex;
  flex-direction: column;

  &__item {
    display: block;
    padding-bottom: 16px;
    @include sm-screen {
      padding-bottom: 10px;
    }
    @include msm-screen {
      padding-bottom: 8px;
    }
    &:last-child {
      padding-bottom: 0;
    }
  }

  &__link {
    color: hsl(var(--white));
    @include sm-screen {
      font-size: 14px;
    }
    &:hover {
      color: hsl(var(--base));
      text-decoration: underline;
    }
  }
}

/* Footer Contact */
.footer-contact-menu {
  &__item {
    margin-bottom: 16px;
    &:last-child {
      margin-bottom: 0;
    }
    .text {
      color: hsl(var(--white));
      font-weight: 600;
    }
    &-content {
      p {
        color: hsl(var(--white));
        @include sm-screen {
          font-size: 14px;
        }
      }
    }
  }
}

/* ============= Footer End Here ======================= */

/*============== top footer css start here ==============*/
.top-footer {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  align-items: center;
  margin-bottom: 40px;
  padding-bottom: 40px;
  border-bottom: 1px solid hsl(var(--white)/.2);
  @include sm-screen {
    flex-direction: column;
    justify-content: flex-start;
  }
  &__mail {
    align-items: center;
    gap: 10px 20px;
    display: grid;
    grid-template-columns: 3fr auto;
    @include xsm-screen {
      grid-template-columns: 1fr;
    }
    .form--control {
      background-color: hsl(var(--white)/.2);
      border-color: hsl(var(--border-color)/.2);
      color: hsl(var(--white));
      &::placeholder {
        color: hsl(var(--white));
        font-size: 14px;
      }
    }
    .btn--white {
      &:hover {
        background-color: hsl(var(--white));
      }
    }
  }
  &__title {
    color: hsl(var(--white));
    font-weight: 600;
    margin-bottom: 4px;
  }
  &__desc {
    font-size: 14px;
    color: hsl(var(--white));
    @include md-screen {
      font-size: 12px;
    }
  }
  &__text {
    color: hsl(var(--white));
    font-size: 12px;
    margin-top: 8px;
  }
  &__left {
    width: calc(100% - 430px);
    @include md-screen {
      width: calc(100% - 350px);
    }
    @include sm-screen {
      width: 100%;
    }
  }
  &__right {
    width: 430px;
    @include md-screen {
      width: 350px;
    }
    @include sm-screen {
      width: 100%;
    }
  }
}
/*============== top footer css end here ==============*/


/* ============= Bottom Footer End Here ======================= */
.bottom-footer {
  border-top: 1px solid hsl(var(--white)/0.2);
  padding: 20px;
  margin-top: 50px;
  &-text {
    font-weight: 600;
    font-size: 12px;
  }
  &__desc {
    color: hsl(var(--white));
    margin-top: 8px;
    font-size: 12px;
  }
}

/* =============Bottom Footer End Here ======================= */