/* ================= Slick Arrow & Dots css Start ================ */
.slick-initialized.slick-slider {
  margin: 0 -10px;

  .slick-track {
    display: flex;
  }

  .slick-slide {
    height: auto;
    padding: 0 10px;

    >div {
      height: 100%;
    }
  }
}

.slick-arrow {
  position: absolute;
  z-index: 1;
  border: 1px solid hsl(var(--base)/.2);
  color: hsl(var(--base)/.1);
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: 0.2s linear;
  background-color: hsl(var(--base)/.1);
  font-size: 20px;
  bottom: -10px;

  &:hover {
    background-color: hsl(var(--section-bg));
    color: hsl(var(--base));
  }
}

.slick-next {
  right: 0px;
}

.slick-prev {
  left: 0px;
}

/* -------------- Slick Slider Arrow Style two ------------  */


/* Dots Css Start */
.slick-dots {
  text-align: center;
  padding-top: 50px;

  li {
    display: inline-block;

    button {
      border: none;
      background-color: hsl(var(--base)/.2);
      color: hsl(var(--white));
      margin: 0 3px;
      width: 8px;
      height: 8px;
      border-radius: 1px;
      border-radius: 50%;
      text-indent: -9999px;
      transition: 0.3s linear;
    }

    &.slick-active button {
      background-color: hsl(var(--base));
      width: 20px;
      border-radius: 5px;
      color: hsl(var(--base));
    }
  }
}

/* Dots Css End */
