/* ================================= Tab Css Start =========================== */
.custom--tab {
  justify-content: center;
  border-radius: 6px;
  margin-bottom: 40px;

  .nav-item {
    border-bottom: 0;
    padding: 5px;

    .nav-link {
      color: hsl(var(--body-color));
      padding: 8px 25px;
      background-color: transparent !important;
      border-radius: 5px;
      transition: .4s;
      border: 1px solid hsl(var(--black) / .08) !important;
      font-size: 14px;
      @include lg-screen {
        padding: 12px 15px;
      }

      &.active {
        color: hsl(var(--white));
        background-color: hsl(var(--base-d-200)) !important;
        border: 1px solid transparent !important;

        &:hover {
          color: hsl(var(--white));
        }
      }

      &:hover {
        color: hsl(var(--base));
      }
    }
  }
  &.tab-two {
    justify-content: flex-start;
    border-radius: 0;
    background-color: hsl(var(--white));
    padding: 0;
    padding-bottom: 15px;
    border-radius:0;
    gap: 40px 30px;
    width: 100%;
    border-bottom: 1px solid hsl(var(--border-color) /.4);
    @include xsm-screen {
      gap: 20px 10px;
    }
    .nav-item {
      padding: 0;

      .nav-link {
        background-color: transparent !important;
        border-radius: 0;
        border: 0 !important;
        padding: 0 10px !important;
        position: relative;
        @include xsm-screen {
          padding: 0 5px !important;
        }
        &::after {
          position: absolute;
          content: "";
          bottom: -17px;
          left: 0;
          width: 0;
          height: 3px;
          background-color: hsl(var(--base-d-400));
          @include xsm-screen {
            bottom: -10px;
          }
        }

        &.active {
          color: hsl(var(--base)) !important;
          background-color: transparent !important;
          box-shadow: none;
          &.nav-link {
            color: hsl(var(--base));
            font-weight: 600;
            &::after {
              width: 100%;
            }
          }

          &:hover {
            color: hsl(var(--base));
          }
        }

      }
    }
  }
}

/* ================================= Tab Css End =========================== */