/*============== insurance plan section start here ==============*/
.plan-section {
    position: relative;
    z-index: 1;
    overflow: hidden;

    &__shape {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        height: 100%;
    }

    &::before {
        position: absolute;
        content: "";
        width: 520px;
        height: 100%;
        left: -400px;
        top: -330px;
        z-index: -1;
        background: linear-gradient(180deg, rgba(219, 223, 240, 0.7) 0%, rgba(62, 79, 152, 0.7) 100%);
        filter: blur(700px);
    }

    &__btn {
        margin-bottom: 32px;

        @include msm-screen {
            margin-top: 20px;
        }
    }

    &__top {
        padding: 24px;
        display: flex;
        justify-content: space-between;
        background: linear-gradient(90deg, #B7BFE1 -93.24%, #3E4F98 100%);
        border-radius: 8px;
        align-items: center;
        gap: 20px 40px;

        @include md-screen {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }

        @include sm-screen {
            grid-template-columns: 1fr;
        }

        @include msm-screen {
            gap: 20px;
        }

        .insurance-info {
            position: relative;

            &::after {
                content: '';
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: -100px;
                width: 1px;
                height: 100%;
                background: hsl(var(--border-color)/.4);

                @include xl-screen {
                    right: -70px;
                }

                @include lg-screen {
                    display: none;
                }
            }

            &:last-child {
                &::after {
                    display: none;
                }
            }

            &__title {
                color: hsl(var(--white));
                margin-bottom: 4px;

                @include msm-screen {
                    font-size: 14px;
                }
            }

            &__text {
                color: hsl(var(--white));
                font-weight: 600;
                font-size: 18px;

                @include msm-screen {
                    font-size: 16px;
                }
            }
        }
    }

    .edit-btn {
        color: hsl(var(--white));
        font-size: 18px;
    }
}

.back-btn {
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 6px;

    &__icon {
        font-size: 18px;
    }
}

.plan-container {
    margin-top: 24px;

    &__title {
        margin-bottom: 20px;
        font-weight: 600;
    }
    .plan-wrapper {
        display: flex;
        gap: 24px;
        align-items: flex-start;
        @include md-screen {
            flex-direction: column;
        }
        &__left {
            width: calc(100% - 300px);
            @include md-screen {
                width: 100%;
            }
        }
        &__right {
            width: 300px;
            position: sticky;
            top: 100px;
            @include md-screen {
                width: 100%;
            }
            .plan-sidebar {
                padding: 20px;
                border-radius: 8px;
                background: hsl(var(--section-bg)/.2);
                border: 1px solid hsl(var(--border-color)/.2);
                margin-bottom: 10px;
                &:last-child {
                    margin-bottom: 10px;
                }
                &__top {
                    margin-bottom: 20px;
                    display: flex;
                    align-items: center;

                }
                &__title {
                    margin-bottom: 0;
                    font-family: var(--body-font);
                    font-weight: 600;
                }
                &__text {
                    font-size: 14px;
                    color: hsl(var(--heading-color));
                }
                .plan-list {
                    &__item {
                        display: flex;
                        align-items: flex-start;
                        gap: 8px;
                        font-size: 14px;
                        margin-bottom: 10px;
                        color: hsl(var(--heading-color));
                        &:last-child {
                            margin-bottom: 0;
                        }
                        &-text {
                            font-weight: 600;
                        }
                    }
                    &__icon {
                        font-size: 12px;
                    }
                }
            }
        }
    }
}

.plan-item {
    background-color: hsl(var(--section-bg));
    padding: 24px;
    border: 1px solid hsl(var(--border-color)/.4);
    border-radius: 8px;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    gap: 20px 40px;
    display: grid;
    grid-template-columns: auto auto auto auto;
    @include lg-screen {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    @include msm-screen {
        grid-template-columns: 1fr;
        gap: 20px;
    }

    @include msm-screen {
        gap: 20px;
    }
    &:last-child {
        margin-bottom: 0;
    }

    .text {
        font-size: 12px;
        font-weight: 600;
        color: hsl(var(--heading-color));
    }
    &__content {
        position: relative;
        .form-check {
            align-items: center;
            .form-check-input {
                margin-top: 0;
            }
        }
        &::after {
            content: '';
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 0px;
            width: 1px;
            height: 70px;
            background: hsl(var(--border-color)/.4);

            @include xl-screen {
                display: none;
            }
        }

        &:last-child {
            &::after {
                display: none;
            }
        }
        &:nth-last-child(2) {
            &::after {
                display: none;
            }
        }
        .btn {
            margin-bottom: 12px;
        }
    }
    &__title {
        margin-bottom: 4px;
        color: hsl(var(--heading-color));
    }
    &__number {
        margin-bottom: 4px;
        font-weight: 600;
        font-family: var(--body-font);
    }
    &__desc {
        font-size: 12px;
        color: hsl(var(--heading-color));
        display: flex;
        align-items: center;
        gap: 3px;
        flex-wrap: wrap;
        .icon {
            color: hsl(var(--base));
        }
    }
}

/*============== insurance plan section end here ================*/