/* ======================  Select2 Start  ======================*/
.select2-wrapper {
  --border-radius: 8px;
  --height: 40px;
  --select2-border: hsl(var(--border-color));

}

.select2 {
  +.select2-container {
    border: 1px solid var(--select2-border);
    border-radius: var(--border-radius);
    height: 40px;
    box-shadow: var(--box-shadow);
    background: hsl(var(--white));

    .select2-selection__rendered {
      line-height: var(--height);
      padding-left: 16px !important;
    }

    .select2-selection--single {
      height: var(--height);
    }

    .select2-selection {
      border: 0;
      background-color: transparent;
      width: 100%;
    }

    .selection {
      width: 100%;
    }

    .select2-selection__arrow {
      top: 50%;
      right: 5px;
      transform: translateY(-50%);
      background-color: transparent;
      border-radius: inherit;
      height: calc(var(--height) - 10px);
      width: calc(var(--height) - 10px);

      &::after {
        position: absolute;
        content: "\f078";
        inset: 0;
        font-weight: 900;
        font-family: "Font Awesome 6 Free";
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.2s;
        font-size: 14px;
      }

      b {
        border: 0;
      }




    }
  }



}


.select2-container--open:has(.select2-dropdown--above) {
  margin-top: -12px;
}

.select2-container--open:has(.select2-dropdown--below) {
  margin-top: 10px;
}

.select2-container--open .select2-dropdown {
  border: 1px solid hsl(var(--black)/.1) !important;
  border-radius: 4px !important;
  overflow: hidden;
  box-shadow: var(--box-shadow-two);
}

.select2-container--open .select2-dropdown--above {
  margin-bottom: 10px;
}

.select2-search__field {
  border-radius: 4px !important;
  border: 1px solid hsl(var(--black)/.1) !important;


  &:focus {
    outline: 0;
    border-color: hsl(var(--base)) !important;
  }
}

.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
  background-color: hsl(var(--base));
}

.select2-container--below.select2-container--open {
  .select2-selection__arrow::after {
    transform: rotate(180deg);
  }
}
.select2-container--default .select2-selection--single .select2-selection__rendered {
  color: hsl(var(--body-color));
  font-weight: 400;
  font-size: 16px;
}
/* ======================  Select2 End  ======================*/

.select2-container:has(.select2-selection--single) {
  width: 100% !important;
}
.select2-container {
  position: relative;
  z-index: 109999;
}