.blog-sidebar-wrapper {
  position: sticky;
  top: 100px;
  border: 1px solid hsl(var(--border-color)/.2);
  background-color: hsl(var(--section-bg) / 0.2);
  padding: 20px;
  border-radius: 16px;
}


/* ================== Sidebar Box & Title =================== */
.blog-sidebar {
  &__title {
    margin-bottom: 10px;
    font-weight: 600;
  }
}

/* ========================= Category & Tags List Style ========================= */


/* ======================== Latest Blog Css========================  */
.latest-blog {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  background-color: hsl(var(--white));
  box-shadow: var(--box-shadow-two);
  border-radius: 8px;
  padding: 8px;
  align-items: center;
  &:last-of-type {
    margin-bottom: 0px;
  }

  &__thumb {
    width: 170px;
    max-height: 130px;
    display: flex;
    border-radius: 4px;
    overflow: hidden;

    @include xsm-screen {
      width: 100px;
    }

    a {
      display: block;
      height: 100%;
      width: 100%;
    }
  }

  &__content {
    width: calc(100% - 170px);
    padding-left: 15px;

    @include xsm-screen {
      width: calc(100% - 100px);
    }
  }

  &__bottom {
    .title {
      font-weight: 600;
      color: hsl(var(--base));
      margin-bottom: 4px;
      font-size: 14px;
    }
  }

  &__title {
    margin-bottom: 16px;
    @include msm-screen {
      margin-bottom: 10px;
    }
    @include xsm-screen {
      margin-bottom: 5px;
    }
    a {
      color: hsl(var(--heading-color));
      font-weight: 600;
      font-size: rem(18px);
      @include line-clamp(2);
      @include xsm-screen {
        font-size: 14px;
      }
      &:hover {
        color: hsl(var(--base));
      }
    }
  }
}

/* ========================= Latest Blog Css End ===================*/