/*=============== choose us section css start here ===============*/
.choose-us-section {
    position: relative;
    overflow: hidden;
    z-index: 1;
}
.choose-us-card {
    height: 100%;
    background-color: hsl(var(--white));
    border-radius: 8px;
    overflow: hidden;
    box-shadow: var(--box-shadow);
    &__thumb {
        max-height: 300px;
        overflow: hidden;
        position: relative;

        &-inner {
            width: 100%;
            height: 100%;

            img {
                transition: 0.6s;
                background-size: cover;
                width: 100%;
            }

            .image1 {
                transform: translatex(50%) scalex(1);
                opacity: 0;
                filter: blur(10px);
            }

            .image2 {
                transform: translatex(0%) scalex(1);
                opacity: 1;
                filter: blur(0px);
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                object-fit: cover;
            }
        }
    }
    &:hover {
        .choose-us-card {
          &__thumb {
            &-inner {
              .image1 {
                transform: translatex(0) scalex(1);
                opacity: 1;
                filter: blur(0);
              }
              .image2 {
                transform: translatex(-50%) scalex(2);
                opacity: 0;
                filter: blur(10px);
              }
            }
          }
        }
      }
    &__content {
        padding: 24px;
    }
    &__title {
        margin-bottom: 10px;
        font-weight: 600;
    }
    &__desc {
        color: hsl(var(--heading-color));
    }
}
.choose-us-slider {
    .slick-list {
        margin-bottom: -15px;
        padding-bottom: 15px;
    }
}
.choose-us-wrapper {
    &__bottom {
        text-align: center;
        margin-top: 50px;
        @include sm-screen {
            margin-top: 30px;
        }
        .title {
            margin-bottom: 24px;
            @include sm-screen {
                margin-bottom: 16px;
            }
        }
    }
}
/*=============== choose us section css end here ===============*/
