/*=============== how work section css start here ===============*/
  .how-work-section {
    position: relative;
    z-index: 1;
    overflow: hidden;
    &__bottom {
        text-align: center;
        margin-top: 50px;
        @include sm-screen {
            margin-top: 30px;
        }
    }
    .left-bg {
        left: -20%;
    }
    .right-bg {
        right: -20%;
    }
  }

  .how-work-item {
    background-color: hsl(var(--white));
    box-shadow: var(--box-shadow);
    padding: 40px 30px;
    border-radius: 8px;
    text-align: center;
    height: 100%;
    @include md-screen {
        padding: 30px 24px;
    }
    @include sm-screen {
        padding: 24px 16px;
    }
    &__thumb {
        max-width: 70px;
        margin: 0 auto;
        margin-bottom: 24px;
        @include sm-screen {
            margin-bottom: 16px;
        }
    }
    &__title {
        margin-bottom: 24px;
        font-weight: 600;
        @include sm-screen {
            margin-bottom: 16px;
        }
    }
  }
/*=============== how work section css end here ===============*/
