/* ================================ Testimonails Section Css Start ============================= */
.testimonials {
  position: relative;
  overflow: hidden;
  z-index: 1;

  .slick-initialized.slick-slider {
    .slick-track {
      margin: 10px 0;
    }
    .slick-slide {
      padding: 0;
    }
  }
  .slick-dots {
    text-align: left;
    @include msm-screen {
      text-align: center;
      padding-top: 30px;
    }
  }

  .slick-prev {
    left: unset;
    right: 64px;
  }
}

.testimonails-card {
  padding: 0 10px;
  height: 100%;
}

.testimonial-item {
  background-color: hsl(var(--white));
  padding: 24px;
  border-radius: 8px;
  height: 100%;
  box-shadow: var(--box-shadow);
  box-shadow: 0px 2px 8px 0px #63636333;

  @include msm-screen {
    padding: 20px 15px;
  }

  &__content {
    margin: 24px 0;
  }

  &__title {
    font-weight: 600;
    color: hsl(var(--heading-color));
    margin-bottom: 8px;
  }

  &__info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__thumb {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    overflow: hidden;
  }

  &__details {
    width: calc(100% - 32px);
    padding-left: 10px;
    line-height: 1;
  }

  &__name {
    margin-bottom: 5px;
    color: hsl(var(--heading-color));
    font-weight: 600;
  }

  &__designation {
    font-size: 12px;
  }

  &__desc {
    color: hsl(var(--heading-color));
  }
}

/* ================================ Testimonails Section Css End ============================= */