/*=============== cta section css start here ===============*/
.cta-section {
    background-color: hsl(var(--base));
    position: relative;
    z-index: 1;
    overflow: hidden;

    &__shape {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        z-index: -1;
        height: 100%;
    }

    .cta-wrapper {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        @include sm-screen {
            display: block;
        }
        &__title {
            color: hsl(var(--white));
            margin-bottom: 8px;
        }
        &__btn {
            margin-top: 40px;
            @include msm-screen {
                margin-top: 20px;
            }
            .btn--white {
                transition: .3s linear;
                font-weight: 600;
                &:hover {
                    background-color: hsl(var(--white)) !important;
                    color: hsl(var(--base)) !important;
                    transform: scale(1.01);
                }
            }
        }
        &__desc {
            color: hsl(var(--white));
            font-size: 18px;
            @include msm-screen {
                font-size: 15px;
            }
        }

        &__left {
            padding: 55px 0;
            @include md-screen {
                padding: 30px 0;
            }    
        }
        &__right {
            max-width: 500px;
            @include sm-screen {
                display: none;
            }
        }
        &__thumb {
            max-height: 400px;
        }
    }
}