/* =========================================== Blog Details Css Start ==============================*/
.blog-details {
  height: 100%;
  &__thumb {
    height: 450px;
    max-height: 100%;
    border-radius: 16px;
    overflow: hidden;
    @include md-screen {
      height: 350px;
    }

    @include sm-screen {
      height: 300px;
    }
  }
  .blog-item {
    &__bottom {
      margin-bottom: 50px;
      @include sm-screen {
        margin-bottom: 24px;
      }
    }
  }
  &__content {
    margin-top: 24px;
  }
  &__share {
    .social-list {
      gap: 5px 24px;
    }
  }
  &__title {
    margin-bottom: 24px;
  }

  &__desc {
    margin-bottom: 24px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  .social-list {
    &__link {
      color: hsl(var(--base));
      transition: .3s linear;
      &:hover {
        color: hsl(var(--base-d-300));
        transform: scale(1.02);
      }
    }
  }
}

.quote-text {
  background-color: hsl(var(--base));
  padding: 20px 24px;
  border-radius: 16px;
  border-left: solid #35353F;
  border-bottom: solid #35353F;
  border-width: 0px, 0px, 4px, 4px;
  &__desc {
    color: hsl(var(--white));
    font-size: 17px;
    font-weight: 400;
  }
  @include sm-screen {
    padding: 25px 15px;
  }
}

.content-wrapper {
  margin-bottom: 24px;
  &__title {
    margin-bottom: 16px;
    font-weight: 600;
  }
  &:last-child {
    margin-bottom: 0;
  }
}
.content-list {
  &__item {
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 600;
    color: hsl(var(--heading-color));
    position: relative;
    padding-left: 20px;
    &::before {
      position: absolute;
      content: '';
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: hsl(var(--black));

    }
  }
  &.list-two {
    .content-list {
      &__item {
        font-size: 16px;
        margin-bottom: 16px;
        color: hsl(var(--body-color));
        font-weight: 400;
      }
    }
  }
}
/* ========================================== Blog Details Css End ======================================*/