/*=============== step section css start here ===============*/
.step-section {
    position: relative;
    z-index: 1;
    overflow: hidden;
    .step-wrapper {
        &__right {
            background-color: hsl(var(--white));
            padding: 24px;
            box-shadow: var(--box-shadow);
            border-radius: 8px;
            height: 100%;
            @include xsm-screen {
                padding: 20px 16px;
            }
        }
        &__verify {
            align-items: center;
            gap: 10px 20px;
            display: grid;
            grid-template-columns: 3fr auto;
            @include xsm-screen {
                grid-template-columns: 1fr;
            }
        }
        .step-list {
            counter-reset: step-counter;
            &__item {
                background-color: hsl(var(--white));
                box-shadow: var(--box-shadow);
                padding: 24px;
                border-radius: 8px;
                display: flex;
                align-items: center;
                gap: 20px;
                counter-increment: step-counter;
                margin-bottom: 20px;
                @include xsm-screen {
                    padding: 20px 16px;
                }
                &:last-child {
                    margin-bottom: 0;
                }
            }
            &__number {
                color: hsl(var(--heading-color));
                font-size: 32px;
                font-weight: 600;
                position: relative;
                content: counter(step-counter);
                padding-right: 20px;
                &::before {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    left: 0;
                    content: counter(step-counter);
                }
                &::after {
                    position: absolute;
                    content: '';
                    right: -3px;
                    bottom: -12px;
                    width: 5px;
                    height: 5px;
                    border-radius: 50%;
                    background-color: hsl(var(--heading-color));
                }
            }
            &__title {
                margin-bottom: 5px;
            }
        }
    }
}
.insurance-details {
    margin-top: 16px;
    padding: 20px;
    background-color: hsl(var(--section-bg));
    border-radius: 4px;
    &__title {
        margin-bottom: 16px;
        color: hsl(var(--heading-color));
        font-size: 14px;
        font-weight: 600;
    }
    &__item {
        display: flex;
        align-items: center;
        gap: 5px;
        flex-wrap: wrap;
        font-size: 14px;
        font-weight: 600;
        margin-bottom: 8px;
        &:last-child {
            margin-bottom: 0;
        }
        .text {
            color: hsl(var(--heading-color));
        }
    }
}
/*================= step section css end here ===============*/
