/* ================================= Social Icon Css Start =========================== */
.social-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 5px 15px;
  &__link {
    position: relative;
    transition: .3s;
    font-size: 28px;
    cursor: pointer;
    color: hsl(var(--white));

    &.active,
    &:hover,
    &:focus {
      color: hsl(var(--base)) !important;
    }

    @include sm-screen {
      @include fs-20;
    }
  }
}

/* ================================= Social Icon Css End ===========================  */