/* ================================= Dashboard Css Start =========================== */
.dashboard {
  position: relative;
  transition: .5s linear;
  &__right {
    width: 100%;
    margin-left: 300px;
    background-color: hsl(var(--white));

    @include md-screen {
      width: 100%;
      margin-left: 0;
    }
  }

  .search-form {
    width: 370px;
    position: relative;

    @include lg-screen {
      width: 300px;
    }

    @include msm-screen {
      width: 100%;
    }

    .form--control {
      background-color: transparent !important;
      padding-left: 40px;
      @include xsm-screen {
        padding-left: 28px;
      }
    }

    &__icon {
      position: absolute;
      left: 10px;
      top: 50%;
      transform: translateY(-50%);
      color: hsl(var(--body-color)/.5);
    }
  }

  // ======================= Dashboard Header Start=======================
  .dropdown {
    display: inline-block !important;
  }

  .dashboard-header {
    padding: 0 24px;
    background-color: hsl(var(--section-bg));
    position: sticky;
    top: 0;
    z-index: 8;

    @include xsm-screen {
      padding: 0 15px;
    }

    &__left {
      display: flex;
      align-items: center;
      gap: 10px;

      .title {
        font-family: var(--body-font);
        margin-bottom: 0;
        color: hsl(var(--body-color));
      }
    }

    &__inner {
      padding: 20px 0;
      gap: 10px;
    }

    &__right {
      gap: 15px;

    }
  }


  /* User Info Css Start */
  .user-info {
    position: relative;
    z-index: 91;

    &__thumb {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      overflow: hidden;
      border: 1px solid hsl(var(--border-color)/.1);

      @include xsm-screen {
        width: 35px;
        height: 35px;
      }

      img {
        object-fit: cover;
      }
    }

    &__button {
      cursor: pointer;
      gap: 15px;
      @extend .flex-align;

      @include msm-screen {
        gap: 10px;
      }

      &:focus-visible {
        outline: none;
        border: none;
      }
    }

    &__desc {
      font-size: 14px;

      .icon {
        margin-left: 10px;
        color: hsl(var(--body-color));
      }
    }

    &__profile {
      @include sm-screen {
        display: none;
      }
    }

    .user-info-dropdown {
      border-radius: 4px;
      overflow: hidden;
      transition: 0.25s linear;
      background-color: hsl(var(--white));
      box-shadow: 0px 5px 25px rgb(0 0 0 / 10%);
      min-width: 200px;
      position: absolute;
      right: 0;
      z-index: 9;
      top: 100%;
      margin-top: 15px;
      padding: 0px;
      transform: scale(.95);
      visibility: hidden;
      opacity: 0;

      &.show {
        visibility: visible;
        opacity: 1;
        transform: scale(1);
        z-index: 991;
      }

      @include md-screen {
        transform: unset !important;
        top: 43px !important;
      }

      &__item {
        &:last-child {
          .user-info-dropdown__link {
            border-bottom: 0 !important;
          }
        }
      }

      &__link {
        padding: 8px 16px;
        color: #5d7387;
        margin-left: 0;
        width: 100%;
        font-weight: 600;
        text-align: left;
        @extend .fs-15;
        border-radius: 0px;

        &.active {
          background-color: hsl(var(--section-bg));
          color: hsl(var(--title-color));
        }

        &:hover {
          background-color: hsl(var(--section-bg));

          .text {
            color: hsl(var(--title-color)) !important;
          }

          .icon {
            color: hsl(var(--title-color)) !important;
          }
        }

        .icon {
          margin-right: 8px;
          transition: .2s;
        }
      }
    }

    &__icon {
      width: 40px;
      height: 40px;
      font-size: 24px;
      color: hsl(var(--white));
      overflow: hidden;
      border-radius: 50%;
      background-color: hsl(var(--body-bg));
      display: flex;
      align-items: center;
      justify-content: center;

      img {
        @extend .fit-image;
      }
    }

    &__name {
      font-size: 18px;
      font-weight: 600;
      margin-bottom: 0px;
    }

  }

  /* User Info Css End */

  // ======================= Dashboard Header End =======================

  // ========== Dashboard Body Start ===============
  .dashboard-body {
    position: relative;
    padding: 24px;

    @include xsm-screen {
      padding: 20px 15px;
    }

    &__bar {
      &-icon {
        color: hsl(var(--body-color));
        font-size: 1.5625rem; //25px
        cursor: pointer;
      }
    }

    &__top {
      display: flex;
      justify-content: space-between;
      gap: 10px 30px;
      margin-bottom: 16px;
      align-items: center;

      @include xsm-screen {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
      }
    }

    &__left {
      min-width: 100px;

      @include xsm-screen {
        width: 100%;
      }
    }

    &__right {
      display: flex;
      gap: 10px;
      align-items: center;
      justify-content: flex-end;
      flex-wrap: wrap;

      @include xsm-screen {
        justify-content: flex-start;
        flex-direction: column;
        width: 100%;
      }

      .search-form {
        width: 250px;

        @include xsm-screen {
          width: 100%;
        }

        .form--control {
          padding: 4px 16px;
          padding-left: 35px;
        }
      }
    }
  }

  // Dashboard Card
  .dashboard-widget {
    padding: 24px;
    border-radius: 8px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    background-color: hsl(var(--white));
    box-shadow: var(--box-shadow-two);
    transition: 0.25s ease-in-out;
    height: 100%;

    &::after {
      position: absolute;
      content: '';
      right: 50px;
      top: -40px;
      width: 140px;
      height: 140px;
      border-radius: 50%;
      z-index: -1;
      background: linear-gradient(180deg, rgba(219, 223, 240, 0.7) 0%, rgba(62, 79, 152, 0.7) 100%);
      filter: blur(70px);
    }

    &:hover {
      transform: translateY(-2px) scale(1.01);
    }

    @include sm-screen {
      padding: 16px;
    }

    &__btn-group {
      .btn {
        margin-bottom: 10px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    &__title {
      margin-bottom: 16px;
      font-weight: 600;
    }

    &__text {
      margin-top: 10px;
      color: hsl(var(--heading-color));
      font-weight: 600;

      @include msm-screen {
        @include fs-13;
      }
    }

    &__btn {
      margin-top: 16px;
    }

    &__shape {
      position: absolute;
      right: 0;
      top: 0;
      z-index: -1;
    }
  }

  // ========== Dashboard Body End ==============
  /* ================================= Dashboard Css End =========================== */
}

.dashboard-table {
  background-color: hsl(var(--white));
  box-shadow: var(--box-shadow-two);
  border-radius: 8px;

  &__top {
    padding: 24px;
  }

  &__title {
    margin-bottom: 0;
  }
}

.dashboard-select {
  @include xsm-screen {
    width: 100%;
  }

  .form--control {
    background-color: hsl(var(--section-bg));
    font-size: 14px;
    font-weight: 600;
    padding-right: 40px;
  }
}

/* info card css here */
.info-main-container {
  gap: 20px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
}

.info-wrapper {
  flex: 1;
  display: flex;
  flex-direction: column;

  &__title {
    margin-bottom: 10px;
  }
}

.info-card {
  background-color: hsl(var(--white));
  box-shadow: var(--box-shadow-two);
  padding: 16px;
  border-radius: 8px;
  flex: 1;
  display: flex;
  flex-direction: column;

}

.info-list {
  &__item {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    gap: 10px;
    margin-bottom: 8px;

    @include xsm-screen {
      font-size: 14px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__title {
    font-weight: 600;
    color: hsl(var(--heading-color));
  }
}

/*=========== policy-list css start here ===========*/
.policy-renewal {
  margin-top: 24px;

  &__header {
    padding: 12px 20px;
    border-radius: 8px;
    background-color: hsl(var(--section-bg));
  }

  &__body {
    margin-top: 24px;

    .text {
      margin-bottom: 6px;
      color: hsl(var(--heading-color));
    }

    .policy-list {
      list-style: auto;
      padding-left: 20px;

      &__item {
        margin-bottom: 6px;
        color: hsl(var(--heading-color));

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    flex-wrap: wrap;
    margin-top: 16px;
    gap: 6px;
    align-items: center;
  }
}

.amount-list {
  background: hsl(var(--section-bg));
  padding: 16px 12px;
  border-radius: 8px;

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px 10px;
    margin-bottom: 8px;
    font-size: 12px;
    font-weight: 600;

    &:last-child {
      margin-bottom: 0;
    }

    &.total {
      color: hsl(var(--heading-color));
      border-top: 1px solid hsl(var(--border-color));
      padding-top: 8px;
    }
  }
}

/*============== insurance plan page css start here ==============*/
.insurance-plan-wrapper {
  border-radius: 8px;
  overflow: hidden;
  background-color: hsl(var(--white));
  box-shadow: var(--box-shadow-two);

  &__header {
    position: relative;
    padding: 32px 40px;
    z-index: 1;
    background: linear-gradient(90deg, #B7BFE1 -93%, hsl(var(--base)) 100%);
    align-items: flex-start;

    @include sm-screen {
      padding: 24px;
    }

    .title {
      color: hsl(var(--white));
      margin-bottom: 6px;
    }

    .text {
      color: hsl(var(--white));
    }
  }

  &__shape {
    position: absolute;
    top: 0;
    right: 0;
    width: 70%;
    height: 100%;
    z-index: -1;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__body {
    padding: 50px 40px;

    @include xxl-screen {
      padding: 40px 30px;
    }

    @include xl-screen {
      padding: 40px 20px;
    }

    @include xsm-screen {
      padding: 30px 16px;
    }
  }
}

.information-container {
  background: hsl(var(--base)/.1);
  padding: 24px;
  border-radius: 8px;

  @include xsm-screen {
    padding: 16px;
  }

  &.information-two {
    background-color: hsl(var(--white));
    box-shadow: var(--box-shadow-two);
  }
}

.info-main-container {
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
}

.plan-info {
  margin-top: 90px;

  @include msm-screen {
    margin-top: 40px;
    padding: 0 40px;
  }

  @include xsm-screen {
    padding: 0 20px;
  }
}

.page-list {
  position: relative;
  display: flex;
  justify-content: space-between;
  counter-reset: step;
  padding: 0 40px;

  @include xl-screen {
    padding: 0 20px;
  }

  @include msm-screen {
    flex-direction: column;
  }

  .nav-item {
    border-bottom: 0;
    padding: 3px;
    position: relative;
    flex: 1;
    text-align: center;

    @include msm-screen {
      padding-bottom: 40px;
      text-align: left;
    }

    &:last-child {
      @include msm-screen {
        padding-bottom: 0;
      }
    }

    &::after {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      width: 20px;
      height: 20px;
      bottom: 8px;
      color: hsl(var(--body-color));
      font-size: 16px;
      top: -26px;
      display: flex;
      justify-content: center;
      align-items: center;
      counter-increment: step;
      content: counter(step);
      z-index: 2;
      font-size: 24px;

      @include xl-screen {
        font-size: 20px;
      }

      @include msm-screen {
        top: 16px;
        left: -7px;
        transform: translate(0, -50%);
        font-size: 18px;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: -17px;
      right: 50%;
      width: 100%;
      height: 6px;
      background: hsl(var(--section-bg));

      @include msm-screen {
        top: 0;
        left: 0;
        right: unset;
        width: 6px;
        height: 100%;
      }
    }

    .nav-link {
      padding: 0 !important;
      background-color: transparent !important;
      border-radius: 5px;
      transition: .4s;
      border: 0 !important;
      position: relative;

      &::before {
        position: absolute;
        content: "";
        top: -38px;
        left: 50%;
        width: 40px;
        transform: translateX(-50%);
        height: 40px;
        border-radius: 50%;
        background-color: hsl(var(--section-bg));
        z-index: 2;

        @include md-screen {
          display: block !important;
        }

        @include msm-screen {
          top: 50%;
          left: -18px;
          transform: translate(0, -50%);
          width: 36px;
          height: 36px;
        }

      }

      &__title {
        margin-top: 10px;
        font-weight: 600;

        @include xl-screen {
          font-size: 15px;
        }

        @include lg-screen {
          font-size: 14px;
        }

        @include sm-screen {
          font-size: 12px;
        }

        @include msm-screen {
          margin-top: 0;
          margin-left: 40px;
          font-size: 14px;
        }
      }
    }

    &.active {
      &.nav-item {
        &::after {
          color: hsl(var(--white));
          content: "\f00c" !important;
          font-family: "Font Awesome 6 Free";
          font-weight: 800;
        }

        &::before {
          background-color: hsl(var(--success));
        }

        .nav-link {
          &::before {
            background-color: hsl(var(--success));
          }

          &__title {
            color: hsl(var(--heading-color));
          }
        }
      }

      .transaction-item {
        &__title {
          color: hsl(var(--white));
        }

        &__text {
          color: hsl(var(--white));
        }
      }
    }
  }
}

.page-list .nav-item:last-child {
  &::before {
    right: -50%;
    width: 200%;

    @include msm-screen {
      top: 0;
      left: 0;
      right: unset;
      width: 6px;
      height: 100%;
      display: none;
    }
  }
}

/* payment page css */
.payment-card {
  border: 1px solid hsl(var(--border-color));
  border-radius: 8px;
  overflow: hidden;

  &__header {
    background-color: hsl(var(--section-bg));
    padding: 32px 40px;

    @include msm-screen {
      padding: 20px;
    }
  }

  &__title {
    margin-bottom: 8px;
  }

  &__text {
    font-size: 14px;
    color: hsl(var(--heading-color));
  }

  &__body {
    padding: 40px;

    @include msm-screen {
      padding: 20px;
    }
  }
}


/*============== insurance plan page css end here ==============*/

/*================ confirmation page start  css ====================*/
.confirmation-container {
  height: calc(100vh - 140px);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: hsl(var(--white));
  padding: 30px;
  box-shadow: var(--box-shadow-two);
  border-radius: 8px;

  @include xsm-screen {
    padding: 15px;
  }

  &__icon {
    text-align: center;
    display: block;
    font-size: 60px;
    color: hsl(var(--base));
    margin-bottom: 30px;
    line-height: 1;

    @include msm-screen {
      margin-bottom: 15px;
    }
  }

  &__title {
    text-align: center;
    margin-bottom: 16px;
    color: hsl(var(--base));
  }

  &__text {
    text-align: center;
    font-size: 18px;
    margin-bottom: 24px;
    color: hsl(var(--base));
  }

  &__body {
    max-width: 650px;
  }

  .confirmation-card {
    max-width: 500px;
    margin: 0 auto;
    background-color: hsl(var(--section-bg));
    padding: 24px;
    border-radius: 8px;

    @include xsm-screen {
      padding: 15px;
    }
  }
}

.notification-item {
  .form-check-label {
    font-size: 16px !important;
  }
}

/*==================== confirmation page end css ====================*/

/*======= setting page css start here =======*/
.authentication-item {
  &__top {
    display: flex;
    gap: 20px 50px;
    align-items: center;
    margin-bottom: 24px;

    @include msm-screen {
      margin-bottom: 16px;
    }
  }

  &__title {
    margin-bottom: 5px;
  }

  &__desc {
    font-size: 14px;
    color: hsl(var(--heading-color));
  }
}

/*======== claim details css start here ========*/
.single-claim-item {
  margin-bottom: 40px;

  &:last-child {
    margin-bottom: 0;
  }
}

.uploader-thumb {
  background-color: hsl(var(--white));
  padding: 24px;
  border-radius: 8px;

  &__top {
    margin-bottom: 16px;

    .title {
      margin-bottom: 6px;
      color: hsl(var(--heading-color));
      font-weight: 600;
    }
  }
}

.image-uploader {
  border: 1px dashed hsl(var(--border-color));
  border-radius: 8px;
  .upload-text {
    text-align: center;
  }
}

.temp-active {
  transition: .4s linear;
}