/* ================================= Blog Section Css Start Here ============================= */
.blog {
  position: relative;
  z-index: 1;
  overflow: hidden;
  &__bottom {
    text-align: center;
    margin-top: 50px;
    @include sm-screen {
      margin-top: 30px;
    }
  }
  .left-bg {
    top: -30%;
    bottom: unset;
  }
}

.blog-item {
  box-shadow: var(--box-shadow-two);
  border-radius: 8px;
  background-color: hsl(var(--white));
  overflow: hidden;
  height: 100%;
  padding: 8px;
  align-items: center;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(310px, auto));
  &.item-two {
    display: block;
  }
  &:hover {
    .blog-item {
      &__thumb {
        &-link {
          .image1 {
            transform: translatex(0) scalex(1);
            opacity: 1;
            filter: blur(0);
          }

          .image2 {
            transform: translatex(-50%) scalex(2);
            opacity: 0;
            filter: blur(10px);
          }
        }
      }
    }
  }

  &__thumb {
    overflow: hidden;
    position: relative;
    border-radius: 4px;
    max-height: 300px;
    &-link {
      width: 100%;
      height: 100%;

      img {
        transition: 0.6s;
        background-size: cover;
        width: 100%;
        height: 100%;
      }

      .image1 {
        transform: translatex(50%) scalex(1);
        opacity: 0;
        filter: blur(10px);
      }

      .image2 {
        transform: translatex(0%) scalex(1);
        opacity: 1;
        filter: blur(0px);
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        object-fit: cover;
      }
    }
  }

  &__content {
    padding: 24px 16px;
    @include md-screen {
      padding: 20px;
    }

    @include xsm-screen {
      padding: 15px;
    }
  }

  &__title {
    margin-bottom: 8px;
    font-weight: 600;

    &-link {
      color: hsl(var(--heading-color));
      @include line-clamp(2);

      &:hover {
        color: hsl(var(--base));
      }
    }
  }

  &__desc {
    margin-bottom: 24px;
    @include line-clamp(3);

    @include sm-screen {
      margin-bottom: 15px;
    }
  }

  &__bottom {
    .title {
      font-weight: 600;
      color: hsl(var(--base));
      margin-bottom: 4px;
      font-size: 14px;
    }
  }
}

.text-list {
  display: flex;
  align-items: center;
  gap: 24px;

  &__item {
    color: hsl(var(--base));
    position: relative;

    &::after {
      position: absolute;
      content: '';
      right: -14px;
      top: 50%;
      transform: translateY(-50%);
      width: 5px;
      height: 5px;
      border-radius: 50%;
      background-color: hsl(var(--base));
    }

    &:last-child {
      &::after {
        display: none;
      }
    }
  }
}

/* Text Border Bottom Animation Css Start */
.border-effect {
  display: inline !important;
  width: 100%;
  background-repeat: no-repeat;
  background-position-y: -2px;
  background-image: linear-gradient(transparent calc(100% - 1px), currentColor 1px);
  -webkit-transition: 0.6s cubic-bezier(0.215, 0.61, 0.355, 1);
  -o-transition: 0.6s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: 0.6s cubic-bezier(0.215, 0.61, 0.355, 1);
  background-size: 0 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.border-effect:hover {
  background-image: linear-gradient(transparent calc(100% - 1px), currentColor 1px);
  background-size: 100% 100%;
}

/* Text Border Bottom Animation Css Ends */
/* ================================= Blog Section Css End Here ================================= */