/*======== popular section css start here ========*/
.popular-section {
    position: relative;
    z-index: 1;
    overflow: hidden;
}

.popular-item {
    background-color: hsl(var(--white));
    border-radius: 8px;
    overflow: hidden;
    height: 100%;
    box-shadow: var(--box-shadow);

    &__thumb {
        max-height: 300px;
        overflow: hidden;
        position: relative;

        &-link {
            width: 100%;
            height: 100%;

            img {
                transition: 0.6s;
                background-size: cover;
                width: 100%;
            }

            .image1 {
                transform: translatex(50%) scalex(1);
                opacity: 0;
                filter: blur(10px);
            }

            .image2 {
                transform: translatex(0%) scalex(1);
                opacity: 1;
                filter: blur(0px);
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                object-fit: cover;
            }
        }
    }
    &:hover {
        .popular-item {
          &__thumb {
            &-link {
              .image1 {
                transform: translatex(0) scalex(1);
                opacity: 1;
                filter: blur(0);
              }
              .image2 {
                transform: translatex(-50%) scalex(2);
                opacity: 0;
                filter: blur(10px);
              }
            }
          }
        }
      }
    &__content {
        padding: 24px;
        @include sm-screen {
            padding: 20px 16px;
        }
    }

    &__title {
        margin-bottom: 24px;

        .btn {
            font-weight: 600;
        }
    }

    &__bottom {
        margin-top: 32px;
    }

    .advantage-wrapper {
        &__title {
            font-weight: 600;
            color: hsl(var(--heading-color));
            margin-bottom: 20px;
        }

        .value-list {
            &__item {
                display: flex;
                align-items: flex-start;
                gap: 12px;
                margin-bottom: 20px;
                @include sm-screen {
                    margin-bottom: 12px;
                }
                &:last-child {
                    margin-bottom: 0;
                }
            }

            &__icon {
                color: hsl(var(--success));
            }
        }
    }
}

/*======== popular section css end here ==========*/