/* ================================= Modal Css Start =========================== */
.custom--modal {
  .modal-header {
    border: 0;
    padding: 32px 40px;
    position: relative;
    z-index: 1;
    background: linear-gradient(90deg, #B7BFE1 -93%, hsl(var(--base)) 100%);
    align-items: flex-start;
    @include sm-screen {
      padding: 24px;
    }
    &__shape {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;

      img {
        width: 100%;
        height: 100%;
      }
    }

    .btn-close {
      margin: 0;
      padding: 0;
      background: none;
      width: unset;
      height: unset;
      opacity: 1;
      background-image: none;
      border-radius: 0;

      &__icon {
        width: 25px;
        height: 25px;
        background-color: hsl(var(--white));
        font-size: 1rem;
        color: hsl(var(--base));
        display: flex;
        justify-content: center;
        align-items: center;
        line-height: 1;
        border-radius: 50%;
        transition: 0.2s linear;

        &:hover {
          background-color: hsl(var(--danger-l-100));
          color: hsl(var(--white));
        }
      }

      :focus {
        box-shadow: none;
      }
    }

    .modal-title {
      color: hsl(var(--white));
      margin-bottom: 10px;
    }

    &__desc {
      color: hsl(var(--white));
      @include xsm-screen {
        font-size: 12px;
      }
    }
  }

  .modal-content {
    border-radius: 10px !important;
    border: 0;
  }

  .modal-body {
    padding: 32px 40px;
    @include sm-screen {
      padding: 24px;
    }
  }

  .modal-footer {
    display: flex;
    flex-wrap: wrap;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    padding: 32px 40px;
    border-top: 1px solid hsl(var(--border-color));
    justify-content: flex-end;
  }
}

/* ================================= Modal Css End =========================== */