/* =========================== Banner Section Start Here ========================= */
.banner-section {
  position: relative;
  z-index: 1;
  padding-bottom: 90px;
  @include xl-screen {
    padding-bottom: 45px;
  }
  &__shape {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    height: 100%;
  }
  .banner-container {
    padding: 45px 0;
  }
  &::before {
    position: absolute;
    content: "";
    width: 520px;
    height: 520px;
    left: -400px;
    top: -330px;
    z-index: -1;
    background: linear-gradient(180deg, rgba(219, 223, 240, 0.7) 0%, rgba(62, 79, 152, 0.7) 100%);
    filter: blur(700px);
  }
}

.banner-content {
  &__subtitle {
    color: hsl(var(--heading-color));
    font-weight: 600;
    margin-bottom: 12px;
    background: linear-gradient(180deg, #FFFFFF 0%, #EFEFF3 100%);
    border-radius: 8px;
    padding: 11px 16px;
    font-size: 14px;
    line-height: 1;
  }
  &__title {
    margin-bottom: 24px;
  }

  &__desc {
    max-width: 660px;
    font-size: 24px;
    font-weight: 600;
    font-family: var(--heading-font);
    @include md-screen {
      font-size: 20px;
    }
    @include msm-screen {
      font-size: 18px;
    }
    @include xsm-screen {
      font-size: 16px;
    }
  }

  &__button {
    margin-top: 40px;
    @include sm-screen {
      margin-top: 24px;
    }
    .btn {
      @include xsm-screen {
        width: 100%;
        flex-grow: 1;
      }
    }
  }
}
.banner-thumb-wrapper {
  text-align: center;
  position: relative;
  &__client {
    position: absolute;
    bottom: 110px;
    left: -30px;
    background: #edeef5;
    padding: 24px 16px;
    box-shadow: 8px 4px 6px 0px #00000033;
    border-radius: 8px;
    @include sm-screen {
      left: 0;
      bottom: 150px;
    }
    @include xsm-screen {
      bottom: 130px;
      padding: 10px;
    }
    .title {
      font-family: var(--body-font);
      margin-bottom: 5px;
    }
    .text {
      color: hsl(var(--heading-color));
      margin-bottom: 16px;
      font-size: 18px;
      @include sm-screen {
        font-size: 16px;
        margin-bottom: 12px;
      }
      @include xsm-screen {
        font-size: 14px;
        margin-bottom: 5px;
      }
    }
  }
}

/* =========================== Banner Section End Here ========================= */


/*================= category css start here =================*/

.category-item {
  background-color: hsl(var(--white));
  border-radius: 8px;
  box-shadow: 0px 2px 8px 0px #63636333;
  padding: 40px 14px;
  text-align: center;
  transition: .2s linear;
  height: 100%;
  width: 100%;
  border-bottom: 4px solid transparent;
  &:hover {
    border-bottom: 4px solid hsl(var(--base));
    border-radius: 8px;
    background: #ecedf5;
  }
  &__thumb {
    margin: 0 auto;
  }
  &__title {
    font-weight: 600;
    margin-top: 16px;
    color: hsl(var(--heading-color));
  }
}

/*=================== category css end here =================*/

.left-bg {
  position: absolute;
  width: 520px;
  height: 520px;
