/* ================================= Button Css Start =========================== */
.btn-check:checked+.btn,
.btn.active,
.btn.show,
.btn:first-child:active,
:not(.btn-check)+.btn:active {
  color: none;
  background-color: none;
  border-color: none;
}

.pill {
  border-radius: 40px !important;
}

.btn {
  color: hsl(var(--white)) !important;
  font-weight: 600;
  padding: 12px 24px;
  border-radius: 8px;
  position: relative;
  z-index: 1;
  border: 1px solid hsl(var(--border-color));
  font-family: var(--body-font);
  display: inline-block !important;
  @include fs-14;
  line-height: 1;

  &:hover,
  &:focus,
  &:focus-visible {
    box-shadow: none !important;
  }

  &:active {
    top: 1px;
  }

  &--lg {
    padding: 14px 24px;
    font-size: 16px;
    font-weight: 400;
  }

  &--sm {
    padding: 9px 18px;
    font-weight: 600;
    font-size: rem(12px);
  }
  &--xsm {
    padding: 5px 8px;
    font-size: rem(12px);
  }
  &--icon {
    width: 35px;
    height: 35px;
    line-height: 35px;
    @include fs-15;
    padding: 0;
  }
  &--base {
    border: 1px solid hsl(var(--border-color)/.2);
    box-shadow: 0px 1px 2px 0px #1012280D, 0px 1px 2px 0px #0F0E3E40, 0px -1px 1px 1px #3B4B9140 inset, 0px 1px 1px 1px #FFFFFF40 inset, 0px 0px 0px 1px #323F7A inset; 
  }
  &--white {
    box-shadow: 0px 1px 2px 0px #1018280D;
    font-weight: 400;
    color: hsl(var(--body-color)) !important;
    background-color: hsl(var(--white));
    &:hover {
      border: 1px solid hsl(var(--body-color)/.2);
      background-color: hsl(var(--base)/.1);
    }
  }
  // ============= Different Color Button Start ==================
  @include btn-style(--base);
  @include btn-style(--primary);
  @include btn-style(--secondary);
  @include btn-style(--danger);
  @include btn-style(--warning);
  @include btn-style(--info);
}

/* ================================= Button Css End =========================== */