/* ================================= Dashboard Fulid Sidebar Css Start =========================== */
.dashboard {

  // Sidebar logo Css Start
  .sidebar-logo {
    background-color: hsl(var(--white)/.1);
    border-bottom: 1px solid hsl(var(--border-color)/.2);
    padding: 24px;
    width: 100%;
    position: relative;
    overflow: hidden;

    img {
      max-width: 170px;
      max-height: 60px;
    }
  }

  // Sidebar logo Css End
  .bar-style {
    position: absolute;
    width: 20px;
    height: 20px;
    right: 0;
    top: 0;
    border-radius: 0;
    background-color: #f0f0f4;

    @include md-screen {
      display: none;
    }

    &::after {
      position: absolute;
      content: "";
      width: 30px;
      height: 20px;
      right: 0px;
      top: -5px;
      border-radius: 16px 0 16px 0;
      background-color: rgb(41, 44, 52);
    }
  }

  // Sidebar Menu Start
  .sidebar-menu {
    height: 100vh;
    background-color: hsl(var(--dark));
    overflow-y: auto;
    z-index: 999;
    transition: 0.3s linear;
    width: 300px;
    border-radius: 0;
    position: fixed;
    left: 0;
    top: 0;
    align-items: start;
    flex-direction: row;

    @include xsm-screen {
      width: 320px;
    }

    &__bar {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      background: hsl(var(--section-bg));
      font-size: 24px;
      padding: 12px 5px;
      border-radius: 16px 0 0 16px;
      color: hsl(var(--heading-color));
      cursor: pointer;

      @include md-screen {
        display: none;
      }

      &::after {
        position: absolute;
        content: '';
        width: 20px;
        height: 20px;
        right: 0;
        bottom: -20px;
        border-radius: 0;
        background-color: #f0f0f4;

        @include md-screen {
          display: none;
        }
      }

      &::before {
        position: absolute;
        content: "";
        width: 30px;
        height: 20px;
        right: 0px;
        bottom: -20px;
        border-radius: 0px 16px 0px 16px;
        background-color: rgb(41, 44, 52);
        z-index: 1;

        @include md-screen {
          display: none;
        }
      }

      i {
        transform: rotate(90deg);
      }
    }

    &::after {
      position: absolute;
      content: "";
      top: 0;
      left: -550px;
      background: linear-gradient(180deg, rgba(219, 223, 240, 0.7) 0%, rgba(62, 79, 152, 0.7) 100%);
      height: 600px;
      width: 600px;
      filter: blur(250px);
      border-radius: 50%;
      z-index: -1;
    }

    &::-webkit-scrollbar {
      width: 3px;
      height: 3px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: hsl(var(--black) / 0.15);
    }

    &__inner {
      width: 100%;
    }

    //=========== Sidebar Show hide Css Start =======
    &.show-sidebar {
      transform: translateX(0);
    }

    @include md-screen {
      transform: translateX(-100%);
      z-index: 9992;
      border-radius: 0;
    }

    &__close {
      position: absolute;
      top: 8px;
      right: 16px;
      color: hsl(var(--white));
      font-size: 1rem;
      transition: 0.2s linear;
      cursor: pointer;
      z-index: 9;
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: hsl(var(--white)/.1);
      border-radius: 50%;

      &:active {
        top: 14px;
      }

      &:hover,
      &:focus {
        background-color: hsl(var(--danger));
        color: hsl(var(--white));
      }
    }

    //=========== Sidebar Show hide Css End =======
    &-list {
      margin-top: 40px;
      padding: 0 24px;

      &__item {
        margin-bottom: 6px;

        &:last-child {
          .sidebar-menu-list__link {
            border-bottom: 0;
          }
        }

        &.active {
          >a {
            color: hsl(var(--white));
            background: linear-gradient(90deg, #B7BFE1 -93.24%, hsl(var(--base)) 100%);
          }
        }

        &.has-dropdown {
          &.active {
            >a {
              color: hsl(var(--white));

              &:after {
                transform: rotate(90deg);
                right: 18px;
                color: hsl(var(--white));
              }
            }
          }

          >a {
            &:after {
              position: absolute;
              content: '\f105';
              font-family: 'Font Awesome 5 Free';
              font-weight: 900;
              font-style: normal;
              display: inline-block;
              font-style: normal;
              font-variant: normal;
              text-rendering: auto;
              text-align: center;
              background: 0 0;
              right: 16px;
              top: 14px;
              transition: 0.1s linear;
              color: hsl(var(--text-color) / 0.6);
              @include fs-13;
            }
          }
        }
      }

      &__link {
        display: inline-block;
        text-decoration: none;
        position: relative;
        padding: 12px 15px;
        width: 100%;
        color: hsl(var(--white));
        @include fs-16;
        border-radius: 8px;
        font-weight: 600;
        transition: width ease-in-out .3s;
        white-space: nowrap;
        &:hover {
          background: linear-gradient(90deg, #B7BFE1 -93.24%, hsl(var(--base)) 100%);
        }

        &.active {
          color: hsl(var(--base));
        }

        .icon {
          margin-right: 8px;
          @include fs-18;
          text-align: center;
          border-radius: 4px;
        }
      }
    }
  }

  // Sidebar Menu End

  // Sidebar Submenu Start
  .sidebar-submenu {
    display: none;

    &.open-submenu {
      display: block;
    }

    &-list {
      padding: 5px 0;

      &__item {
        margin-bottom: 6px;

        &.active {
          >a {
            color: hsl(var(--white));
            background: linear-gradient(90deg, #B7BFE1 -93.24%, hsl(var(--base)) 100%);
          }
        }
      }

      &__link {
        padding: 12px 15px;
        display: block;
        color: hsl(var(--white));
        margin-left: 20px;
        border-radius: 8px;
        position: relative;
        padding-left: 25px;
        transition: widthease-in-out 0.3s;
        white-space: nowrap;
        &::before {
          position: absolute;
          content: '';
          left: 10px;
          top: 50%;
          transform: translateY(-50%);
          width: 10px;
          height: 10px;
          background-color: transparent;
          border: 1px solid hsl(var(--white) / 0.4);
          border-radius: 50%;
        }

        &:hover {
          background: linear-gradient(90deg, #B7BFE1 -93.24%, hsl(var(--base)) 100%);
        }

        .icon {
          margin-right: 8px;
          @include fs-15;
          text-align: center;
          border-radius: 4px;
        }
      }
    }
  }

  // Sidebar Submenu End
}

/* ============ User Profile Css Start ============ */

/* Sidebar User Info Css */
.user-profile {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid hsl(var(--base-two) / 0.15);
  width: 100%;
  padding: 20px 24px;
  position: sticky;
  bottom: 0;
  left: 0;
  width: calc(300px - 1px);
  background-color: hsl(var(--white));
  align-self: flex-end;
}

.user-profile-info {
  display: flex;
  flex-wrap: wrap;

  &__icon {
    width: 50px;
    height: 50px;
    border-radius: 8px;
    overflow: hidden;
    background-color: #f3f9fe;
    @include fs-18;
    border: 1px solid hsl(var(--black) / 0.1);
  }

  &__content {
    width: calc(100% - 50px);
    padding-left: 15px;
  }

  &__name {
    margin-bottom: 0px;
  }

  &__desc {
    color: hsl(var(--body-color));
    font-size: 15px;
  }
}

.dashboard {
  &.show-hide {
    .sidebar-menu {
      // width: 100px;

      .sidebar-menu-list {
        &__link {
          padding: 12px 17px;
          .icon {
            margin-right: 0;
          }
        }

        .text {
          display: none;
          width: 0;
          white-space: nowrap;
        }
      }

      .has-dropdown {
        .sidebar-submenu {
          display: none !important;
        }
      }
    }

    .sidebar-logo {
      padding: 24px 16px;
    }


    .sidebar-menu-list__item.has-dropdown>a:after {
      display: none;
    }
  }
}

/* ========= User Profile Css End ============== */

/* ================================= Dashboard Fulid Sidebar Css End =========================== */