/*============== insurance compare section start here ==============*/
.compare-section {
    position: relative;
    z-index: 1;
    overflow: hidden;

    &__shape {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        height: 100%;
    }

    &::before {
        position: absolute;
        content: "";
        width: 520px;
        height: 500px;
        left: -400px;
        top: -330px;
        z-index: -1;
        background: linear-gradient(180deg, rgba(219, 223, 240, 0.7) 0%, rgba(62, 79, 152, 0.7) 100%);
        filter: blur(300px);
    }


    .edit-btn {
        color: hsl(var(--white));
        font-size: 18px;
    }
}

.section-btn {
    margin-bottom: 32px;

    @include msm-screen {
        margin-top: 20px;
    }
}

.compare-container {
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 10px 20px;
        margin-bottom: 24px;

        @include xsm-screen {
            justify-content: flex-start;
            flex-wrap: wrap;
            align-items: flex-start;
        }

        .title {
            margin-bottom: 0;
        }
    }
}

.back-btn {
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 6px;

    &__icon {
        font-size: 18px;
    }
}

.compare-item {
    background-color: hsl(var(--section-bg));
    padding: 24px;
    border-radius: 8px;

    @include xsm-screen {
        padding: 16px;
    }

    &__text {
        color: hsl(var(--heading-color));
        font-weight: 600;
    }

    &__close-icon {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        color: hsl(var(--white));
        background-color: #cacacf;
        cursor: pointer;
        transition: .2s;

        &:hover {
            background-color: hsl(var(--danger));
        }
    }

    &__top {
        display: flex;
        justify-content: space-between;
        margin-bottom: 24px;

        @include msm-screen {
            margin-bottom: 16px;
        }
    }

    &-wrapper {
        display: flex;
        align-items: center;
        gap: 10px 90px;

        @include xl-screen {
            gap: 10px 40px;
        }

        @include lg-screen {
            justify-content: space-between;
        }

        @include xsm-screen {
            gap: 20px;
            flex-wrap: wrap;
        }

        &__content {
            position: relative;

            &::after {
                content: "";
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: -45px;
                width: 1px;
                height: 50px;
                background: hsl(var(--border-color) / 0.4);

                @include xl-screen {
                    right: -20px;
                }

                @include lg-screen {
                    display: none;
                }
            }

            &:last-child {
                &::after {
                    display: none;
                }
            }
        }

        &__title {
            margin-bottom: 4px;
            color: hsl(var(--heading-color));
        }

        &__number {
            margin-bottom: 4px;
            font-weight: 600;
            font-family: var(--body-font);
        }

        &__desc {
            font-size: 12px;
            color: hsl(var(--heading-color));
            display: flex;
            -webkit-box-align: center;
            align-items: center;
            gap: 3px;
            flex-wrap: wrap;

            .icon {
                color: hsl(var(--base));
            }
        }
    }
}

.compare-table {
    margin: 0;
    margin-top: 12px;
    border-collapse: collapse;
    border-collapse: separate;
    border-spacing: 0px 0px;

    tbody {
        border: 0 !important;

        tr {

            .title {
                background-color: hsl(var(--section-bg)/.2) !important;
                color: hsl(var(--heading-color)) !important;
                font-weight: 600 !important;
                padding: 16px !important;

                @include xsm-screen {
                    padding: 12px !important;
                }
            }

            &:last-child {
                border-bottom: 0;
            }

            &:last-child {
                td {
                    border-bottom: 1px solid hsl(var(--border-color) / 0.4) !important;

                    &:first-child {
                        border-radius: 0px;
                    }

                    &:last-child {
                        border-radius: 0;

                    }
                }
            }

            td {
                text-align: center;
                vertical-align: middle;
                padding: 16px;
                border-width: 1px;
                color: hsl(var(--heading-color));
                font-weight: 400;
                max-width: 170px;
                @include fs-16;
                border: 0;
                background-color: hsl(var(--white));
                border: 1px solid hsl(var(--border-color) / 0.4);
                border-right: 0;
                border-bottom: 0;

                @include xl-screen {
                    @include fs-14;
                }

                @include xsm-screen {
                    padding: 10px;
                    font-size: 12px;
                }

                &::before {
                    content: attr(data-label);
                    font-family: var(--heading-font);
                    @include fs-15;
                    color: hsl(var(--black));
                    font-weight: 500;
                    display: none;
                    width: 45% !important;
                    text-align: left;
                }

                &:first-child {
                    text-align: left;
                    font-weight: 400;
                    color: hsl(var(--body-color));
                    border-left: 1px solid hsl(var(--border-color) / 0.4);
                }

                &:last-child {
                    text-align: left;
                    border-right: 1px solid hsl(var(--border-color) / 0.4);
                }

                &:nth-child(2) {
                    text-align: left;
                }
            }
        }
    }
}

/*============== insurance compare section end here ================*/