/* ============= Header Start Here ======================= */
.navbar-brand {
  padding-top: 0;
  padding-bottom: 0;
  margin-right: 40px;
  &.logo {
    img {
      max-width: 180px;
      @include md-screen {
        max-width: 150px;
      }
    }
  }
}
.header {
  position: relative;
  z-index: 999;
  background: hsl(var(--white));
  border: 1px solid hsl(var(--border-color)/.4);
  .navbar {
    position: unset;
  }
  &.fixed-header {
    position: sticky;
    transition: .3s linear;
    top: 0px;
    animation: slide-down .8s;
    width: 100%;
    background-color: hsl(var(--white));
    border-bottom: 1px solid hsl(var(--black) / .1);
  }
}
@keyframes slide-down {
  0% {
    opacity: 0;
    transform: translateY(-150%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.navbar {
  padding: 0 !important;
}
/* ========================= Desktop Device Start ========================= */
.header {
  @media (min-width: 1200px) {
    .nav-menu {
      padding-top: 0;
      padding-bottom: 0;
      .nav-item {
        padding-right: 34px;
        @include xl-screen {
          padding-right: 20px;
        }
        &:last-child {
          padding-right: 0;
        }
        &.active {
          .nav-link {
            color: hsl(var(--base)) !important;
            &::before {
              width: 100%;
            }
          }
        }
        &:hover {
          .nav-link {
            color: hsl(var(--base)) !important;
          }
          .header-dropdown {
            visibility: visible;
            opacity: 1;
            transform: scale(1) !important;
          }
        }
        .nav-link {
          color: hsl(var(--body-color)) !important;
          position: relative;
          cursor: pointer;
          font-size: rem(16px);
          padding: 30px 6px;
          font-weight: 600;
        }
      }
    }
  }

}
/* ========================== Desktop Device End ========================= */
/* ============================== Small Device ======================= */
.header {
  @include lg-screen {
    padding-block: 20px;
    border-bottom: 1px solid hsl(var(--black) / .05);
    .nav-menu {
      .nav-item {
        text-align: left;
        display: block;
        position: relative;
        margin: 0;
        &:hover .nav-link .nav-item__icon {
          transform: rotate(0deg) !important;
        }
      }
      .header-dropdown {
        position: static !important;
        display: none !important;
        border: 0 !important;
        margin-top: 20px;
        padding-left: 20px;
        .dropdown-menu {
          &__list {
            border-bottom: 1px solid hsl(var(--border-color)/.3);
            padding-bottom: 10px;
            margin-bottom: 10px;
            &:last-child {
              border-bottom: 0;
              margin-bottom: 0;
              padding-bottom: 0;
            }
          }
          &__link {
            padding: 0;
            &:hover {
              background-color: transparent !important;
            }
          }
        }
        &.show {
          display: block !important;
        }
      }
    }
    .nav-item {
      &:first-child {
        border-bottom: none;
      }
      &:not(:last-child) {
        margin-bottom: 16px !important;
      }
      &.active {
        .nav-link {
          color: hsl(var(--base));
        }
      }
      .nav-link {
        padding: 0 !important;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 !important;
        color: hsl(var(--heading-color));
        font-weight: 600;
        font-size: rem(16px);
        &.show {
          &[aria-expanded="true"] {
            color: hsl(var(--base)) !important;
            i {
              transform: rotate(180deg);
            }
          }
        }
      }
    }
  }
  .navbar-toggler {
    &.header-button {
      border-color: transparent;
      color: hsl(var(--black));
      background: transparent !important;
      padding: 0 !important;
      border: 0 !important;
      border-radius: 0 !important;
      transition: .15s ease-in-out;
      width: auto;
      font-size: 2.125rem;
      order: 1;
      margin-left: 10px;
      @include msm-screen {
        font-size: 2rem;
      }
      &:focus {
        box-shadow: none !important;
      }
      &[aria-expanded="true"] i::before {
        content: "\f00d";
      }
    }
  }
}
.header-right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 8px;
  padding-left: 48px;
  @include xl-screen {
    gap: 12px;
    padding-left: 24px;
  }
  .btn {
    &:has(.icon) {
      display: flex !important;
      align-items: center;
      gap: 8px;
      .icon {
        display: flex;
      }
    }
  }
}
.lang-box {
  margin-right: 8px;
  @include xl-screen {
    margin-right: 0;
  }
  &-btn {
    color: hsl(var(--black));
    display: flex;
    gap: 4px;
    align-items: center;
    position: relative;
    @include lg-screen {
      width: 100%;
      border: 1px solid hsl(var(--border-color)/.4);
      border-radius: 8px;
      padding: 8px 10px;
    }
    @include xxsm-screen {
      padding: 6px 10px;
    }
    &.show {
      .icon {
        transform: rotate(180deg);
      }
    }
    .thumb {
      width: 24px;
      height: 24px;
      border-radius: 50%;
      flex-shrink: 0;
      overflow: hidden;
      img {
        display: flex;
      }
      @include xl-screen {
        height: 20px;
        width: 20px;
      }
    }
    .text {
      color: hsl(var(--body-color));
      font-size: rem(16px);
      font-weight: 600;
      line-height: 140%;
      @include xxsm-screen {
        font-size: 13px;
      }
    }
    .icon {
      color: hsl(var(--body-color));
      font-size: rem(12px);
      transition: all linear 0.3s;
    }
  }
  &-link {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 8px 10px;
    font-size: 16px;
    color: hsl(var(--body-color));
    font-weight: 600;
    transition: all .15s linear;
    &:hover {
      background-color: hsl(var(--black) / .05);
      color: hsl(var(--black));
    }
    .thumb {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      overflow: hidden;
      flex-shrink: 0;
      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  .dropdown-menu {
    margin-top: 30px;
    min-width: 130px;
    @include lg-screen {
      left: 0 !important;
      margin-top: 10px;
      right: auto !important;
      width: 100%;
      min-width: 100px;
    }
  }
}
.dropdown-menu {
  padding: 0px;
  border: 0;
  box-shadow: none;
  background-color: hsl(var(--white));
  border-radius: 0px;
  left: auto !important;
  right: 0 !important;
  display: unset !important;
  visibility: hidden;
  opacity: 0;
  transition: all .15s linear;
  transform: scale(0.95);
  top: 100% !important;
  position: absolute !important;
  border: 1px solid hsl(var(--border-color)/.4);
  overflow: hidden;
  &.show {
    visibility: visible;
    opacity: 1;
    transform: scale(1) !important;
  }
  &__link {
    color: hsl(var(--body-color));
    font-weight: 400;
    padding: 8px 15px;
    &:hover {
      background-color: hsl(var(--base)) !important;
      color: hsl(var(--white));
    }
  }
}
.header {
  .offcanvas-header {
    background-color: hsl(var(--white));
  }
  .btn-close {
    box-shadow: none;
  }
}
.account-btn-groups {
  .btn {
    @include xsm-screen {
      font-size: 14px;
      padding: 10px 12px;
    }
    @include xxsm-screen {
      font-size: 13px;
      padding: 10px;
    }
  }
}
/* ================================= Header Css End =========================== */