/*============== counter section css start here ==============*/
.counter-section {
    background-color: hsl(var(--base));
    position: relative;
    z-index: 1;
    overflow: hidden;
    padding: 80px 0;
    @include msm-screen {
        padding: 60px 0;
    }
    &__shape {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        img {
            height: 100%;
        }
    }
    .section-heading {
        &__title {
            color: hsl(var(--white));
        }
    }
}

.counter-item {
    text-align: center;
    max-width: 350px;
    margin: 0 auto;
    &__title {
        color: hsl(var(--white));
        font-weight: 600;
        margin-bottom: 10px;
    }
    &__desc {
        color: hsl(var(--white));
    }
    &__number {
        color: hsl(var(--white));
        font-size: 20px;
        font-weight: 600;
        background: hsl(var(--white) / 0.2);
        border-radius: 8px;
        display: inline-flex;
        -webkit-box-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        align-items: center;
        margin: 0 auto;
        margin-bottom: 20px;
        padding: 5px 15px;
        @include msm-screen {
            margin-bottom: 12px;
            font-size: 18px;
        }
    }
}


.odometer.odometer-auto-theme {
    font-size: 22px;
    @include msm-screen {
        font-size: 20px;
    }
}
/*================ counter section css end here ==============*/
