/* ================================= Card Css Start =========================== */
.custom--card {
  border-radius: 8px;
  box-shadow: var(--box-shadow-two);
  background-color: hsl(var(--white));;
  border: transparent;
  height: 100%;
  .card-header {
    padding: 12px 24px;
    background-color: transparent;
    border-bottom: 1px solid hsl(var(--black)/.1);

    .title {
      margin-bottom: 0;
    }
  }

  .card-body {
    background-color: hsl(var(--white));
    padding: 24px;
    border-radius: 8px;
    .card-title {
      margin-bottom: 24px;
    }
  }

  .card-footer {
    padding: 12px 24px;
    background-color: transparent;
    border-top: 1px solid hsl(var(--black)/.1);
  }
}

/* ================================= Card Css End =========================== */