/* =========================== Accordion Css start ============================= */
.custom--accordion {
  .accordion-item {
    box-shadow: var(--box-shadow);
    background-color: hsl(var(--white)) !important;
    border-radius: 8px;
    overflow: hidden;
    border: 0;
    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  .accordion-header {
    line-height: 1;
  }

  .accordion-body {
    padding: 24px;
    background-color: hsl(var(--white));
    padding-top: 0;
    @include msm-screen {
      padding: 15px;
      padding-top: 0;
    }
    .text {
      max-width: 95%;
      font-weight: 500;
      @include msm-screen {
        max-width: 100%;
        font-size: 15px;
      }
    }
  }

  &:first-of-type .accordion-button.collapsed {
    border-radius: 5px;
  }

  &:last-of-type .accordion-button.collapsed {
    border-radius: 5px;
  }

  .accordion-button {
    background-color: hsl(var(--white));
    color: var(--heading-color);
    font-size: rem(18px);
    padding: 24px;
    font-weight: 600;
    @include sm-screen {
      padding-right: 40px;
    }
    @include msm-screen {
      padding: 15px;
      font-size: rem(16px);
    }

    &::after {
      background-image: none;
    }

    &:focus {
      box-shadow: none;
    }

    &:not(.collapsed) {
      background-color: hsl(var(--white)) !important;
      box-shadow: none;

      &::after {
        background-image: none;
        color: hsl(var(--heading-color));
      }
    }

    &[aria-expanded='true'],
    &[aria-expanded='false'] {
      &::after {
        font-family: 'Font Awesome 6 Free';
        font-weight: 900;
        content: '\f068';
        display: inline-block;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 24px;
        height: unset;
        font-size: 24px;
        @include msm-screen {
          font-size: 20px;
          right: 12px;
        }
      }
    }

    &[aria-expanded='false'] {
      &::after {
        content: '\f067';
        color: hsl(var(--heading-color));
      }
    }
  }
}




/* ================================= Accordion Css End =========================== */
