/* =============================== Brand Section Css Start ======================= */
.brand-section {
  position: relative;
  z-index: 1;
  background-color: hsl(var(--section-bg));
  .slick-track {
    align-items: center;
  }
}
.client-logos {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;

  &:last-of-type {
    margin-bottom: 0;
  }

  img {
    width: 160px !important;
  }

  .slick-slide {
    margin: 0 10px;
  }
}

// Silent Page Logo 
.client-logo {
  background-color: hsl(var(--white));
  padding: 40px 20px;
  box-shadow: var(--box-shadow);
  text-align: center;
  border-radius: 5px;
  border: 1px solid hsl(var(--black) / .08);
  transition: .3s linear;

  &:hover {
    background-color: hsl(var(--base-two));
  }
}

/* =============================== Brand Section Css End ======================= */