/*=========== contact section css start here ===========*/
.contact-section {
    position: relative;
    z-index: 1;
    overflow: hidden;
    .left-bg {
        top: -270px;
        bottom: unset;
        width: 520px;
        height: 620px;
        left: -350px;
    }
}
.contact-item {
    display: flex;
    flex-wrap: wrap;
    margin-top: 16px;
    align-items: center;
    &__icon {
        width: 20px;
        color: hsl(var(--base));
        font-size: 20px;
    }

    &__content {
        width: calc(100% - 20px);
        padding-left: 10px;
    }
    &__desc {
        a {
            color: hsl(var(--body-color));
        }
    }
}


.contact-wrapper {
    background-color: hsl(var(--white));
    padding: 32px;
    border-radius: 8px;
    box-shadow: var(--box-shadow-two);
    &__map {
        width: 100%;
        iframe {
            height: 100%;
            border-radius: 10px;
            width: 100%;
            min-height: 400px;
        }
    }
}
/*============= contact section css end here ===========*/