/*================= area section css start here =================*/
.area-section {
    position: relative;
    z-index: 3;
    overflow: hidden;
    // &__shape {
    //     position: absolute;
    //     top: 0;
    //     left: 0;
    //     width: 100%;
    //     height: 100%;
    //     z-index: -1;
    //     img {
    //         width: 100%;
    //         height: 100%;
    //     }
    // }
    .left-bg, .right-bg {
        filter: blur(300px);
    }
}
.area-form {
    background: hsl(var(--base));
    padding: 32px;
    box-shadow: var(--box-shadow);
    border-radius: 8px;
    @include msm-screen {
        padding: 20px;
    }
    .form--label {
        color: hsl(var(--white));
    }
    .form--control {
        &:focus {
            border-color: hsl(var(--white));
        }
    }
}


/*================== area section css end here =================*/
