/* =========================================== Account Css Start =========================*/
.account {
    padding: 75px 0;
    @include sm-screen {
        padding: 40px 0;
    }
    &.register {
        .account-form-wrapper {
            max-width: 530px;
        }
    }
}
.account-form-wrapper {
    background-color: hsl(var(--white));
    max-width: 480px;
    padding: 48px;
    border-radius: 8px;
    @include msm-screen {
        padding: 20px;
    }
    &__text {
        text-align: center;
        font-size: rem(14px);
        color: hsl(var(--text-color));
    }
    &__title {
        margin-bottom: 8px;
        text-align: center;
        color: hsl(var(--base));
    }
}

.account-form {
    margin-top: 30px;
    @include xsm-screen {
        margin-top: 20px;
    }
    &__text {
        text-align: center;
        font-size: 14px;
        color: hsl(var(--base));
        a {
            color: hsl(var(--base));
            font-weight: 600;
            text-decoration: underline;
        }
    }

    .forgot-password {
        color: hsl(var(--heading-color));
        font-size: 14px;
        margin-bottom: 16px;
    }
}


.account-inner {
    display: flex;
    justify-content: flex-end;
}


.social-login-wrapper {
    text-align: center;
    margin: 30px 0;
    border-top: 1px solid hsl(var(--border-color));
    padding-top: 30px;
}

.social-login-list {
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 16px;

    .social-login-btn {
        border: 1px solid hsl(var(--border-color));
        box-shadow: 0px 1px 2px 0px #1018280D;
        border-radius: 8px;
        padding: 10px 15px;
        color: hsl(var(--heading-color));
        font-size: 14px;
        font-weight: 600;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 5px;
        line-height: 1.3;
        transition: .2s linear;

        &:hover {
            border-color: hsl(var(--base));
            color: hsl(var(--base));
        }
    }
}
.form--check {
    .link {
        color: hsl(var(--heading-color));
        font-weight: 600;
        color: #344054;
    }
}
/* ============================================ Account Css End ====================================*/