/* ================================= Table Css Start =========================== */
/* Table Content Css start */
.action-buttons {
  @extend .flex-align;
  gap: 4px;
  justify-content: flex-end;
}

.action-btn {
  width: 35px;
  height: 35px;
  border-radius: 50%;
}

.edit-btn {
  color: hsl(var(--info));
}

.delete-btn {
  color: hsl(var(--danger));
  background-color: hsl(var(--danger) / 0.08);
}

/* Table Content Css end */

/* Table Css Start */
.table {
  margin: 0;
  border-collapse: collapse;
  border-collapse: separate;
  border-spacing: 0px 0px;

  thead {
    tr {
      th {
        background-color: hsl(var(--section-bg));
        text-align: center;
        padding: 15px 20px;
        font-family: var(--heading-font);
        font-weight: 600;
        max-width: 170px;
        @include fs-12;
        border-top: 1px solid hsl(var(--border-color)/.4);
        border-bottom: 1px solid hsl(var(--border-color)/.4);
        &:not(:first-child) {
          border-left: 0;
        }
        
        &:first-child {
          border-radius: 8px 0 0 0;
          text-align: left;
          border-left: 1px solid hsl(var(--border-color)/.4);
        }

        &:last-child {
          text-align: right;
          border-radius: 0 8px 0 0;
          border-right: 1px solid hsl(var(--border-color)/.4);
        }

        &:nth-child(2) {
          text-align: left;
        }
      }
    }
  }

  tbody {
    border: 0 !important;
    tr {

      &:last-child {
        border-bottom: 0;
      }

      &:last-child {
        td {
          &:first-child {
            border-radius: 0px 0 0 6px;
          }

          &:last-child {
            border-radius: 0 0px 6px 0;
          }
        }
      }

      td {
        text-align: center;
        vertical-align: middle;
        padding: 24px ;
        border-width: 1px;
        color: hsl(var(--body-color));
        font-weight: 500;
        max-width: 170px;
        @include fs-16;
        border: 0;
        background-color: transparent;
        border-bottom: 1px solid hsl(var(--border-color) / 0.4);
        @include xl-screen {
          @include fs-14;
          padding: 20px 16px;
        }
        &::before {
          content: attr(data-label);
          font-family: var(--heading-font);
          @include fs-15;
          color: hsl(var(--black));
          font-weight: 500;
          display: none;
          width: 45% !important;
          text-align: left;
        }

        &:first-child {
          text-align: left;
          font-weight: 600;
          color: #101828;
          border-left: 1px solid hsl(var(--border-color) / 0.4);
        }

        &:last-child {
          text-align: right;
          border-right: 1px solid hsl(var(--border-color) / 0.4);
        }
        &:nth-child(2) {
          text-align: left;
        }
      }
    }
  }
}

.table--responsive--md {
  @include sm-screen {
    thead {
      display: none;
    }

    tbody {
      tr {
        display: block;

        &:last-child td {
          border-bottom: 0;
        }

        td {
          display: flex;
          align-items: center;
          justify-content: space-between;
          gap: 15px;
          text-align: right;
          padding: 10px 15px;
          border: none;
          border-bottom: 1px solid hsl(var(--black) / 0.8);
          max-width: unset;
          &:last-child {
            border: none;
          }

          &:first-child {
            text-align: right;
            border-left: 0;
          }

          &::before {
            display: block;
            @include fs-14;
            color: hsl(var(--black) / 0.7);
          }
        }
      }
    }
  }

  @include sm-screen {
    tbody {
      tr {
        td {
          border: 0;
        }
      }
    }
  }
}

